import PressLoading from 'press-ui/press-loading/press-loading.vue';


export default {
  i18n: {
    'zh-CN': {
      buttonMode: '按钮模式',
      pageMode: '页面模式',
      customBackground: '指定背景色',
      click: '点击加载',
      disappear: '三秒后消失',
    },
    'en-US': {
      buttonMode: 'Button Mode',
      pageMode: 'Page Mode',
      customBackground: 'Custom Background',
      click: 'Check',
      disappear: 'Check',
    },
  },
  components: {
    PressLoading,
  },
  data() {
    return {
      options: {
        btn: {
          show: false,
        },
        page: {
          show: false,
        },
        bg: {
          show: false,
        },
      },
    };
  },
  methods: {
    onShowLoading(type) {
      this.options[type].show = true;
      setTimeout(() => {
        this.options[type].show = false;
      }, 3000);
    },
  },
};
